import { useState } from 'react';
import { Typography, Grid } from "@mui/material";
import { styled } from "@mui/system";
import {useHistory} from 'react-router-dom';
import Footer from "../footer/Footer";
import Navbar from "../navbar/Navbar";
import useMediaQuery from '@mui/material/useMediaQuery';
import TypeSelector from '../WineFilter/TypeSelector';
import Address from '../Profile/Address/Address';
import TypeDropdown from '../TypeDropdown';
import OrderHistory from '../Profile/OrderHistory/OrderHistory';
import SpiritBreadcrumb from '../SpiritBreadcrumb';
import SpiritButton from '../SpiritButton';
import Profile from '../Profile/Profile/Profile';
import {useTranslation} from "react-i18next";

const ContentWrapper = styled('div')`
    max-width: 1770px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 75px;
`

const Wrapper = styled('div')`
    background-color: #f5f5f5;
`
const FooterWrapper = styled('div')`
  width: 100%;
  background-color: #202020;
  height: 100%;
  margin-top: 205px;
  color: #fbfbfb;
`

const LeftMenu = styled(Grid)`
  background-color: #fff;
  border: solid 1px #202020;
  border-radius: 5px;
  padding-left: 47px;
  padding-right: 40px;
  padding-top: 26px;
  max-height: 600px;
`


const displayComponent = (componentForDisplay) => {
  switch(componentForDisplay){
      case 'Address':
          return <Address desktop />
      case 'Order History':
          return <OrderHistory />
      case 'My Profile':
          return <Profile />
      default:
          return <div>Something went wrong</div>
  }
}



const ProfilePage = () => {
    const [component, setComponent] = useState('Address');
    const {t} = useTranslation();

    const matches = useMediaQuery('(max-width:900px)');
    const history = useHistory();
    const isLoggedIn = sessionStorage.getItem('logged_in');

    if(!isLoggedIn) history.push('/');

    const handleLogout = () => {
      sessionStorage.removeItem('logged_in');
      sessionStorage.removeItem('username');
      history.push('/');
    }

    return (
      <Wrapper>
        <Navbar fixed />
        <ContentWrapper>
          <Grid item container>
            <SpiritBreadcrumb path={['Home', 'Profile']} />
          </Grid>
          <Grid item container pt="81px">
            {!matches && (
              <>
                <LeftMenu item xs={3}>
                  <Typography variant="defheader" component="div" sx={{pb: '25px'}} fontSize="45px">{sessionStorage.getItem('username')}</Typography>
                  <TypeSelector 
                    types={['My Profile', 'Address', 'Order History']}
                    handleFilters={(filters) => setComponent(filters)}
                    selected='Address'
                    update={0}
                  />
                  <SpiritButton sx={{width: '100%', mt: '172px', mb: '30px'}} onClick={handleLogout}>{t("Log Out")}</SpiritButton>
                </LeftMenu>
                <Grid item xs={9} sx={{pl: '131px', pt: '30px'}}>
                  <Grid item container sx={{maxWidth: '850px'}}>
                    {displayComponent(component)}
                  </Grid>
                </Grid>
              </>
            )}
            {matches && (
                <>  
                    <Typography variant="defheader" component="div" fontSize="25px">{sessionStorage.getItem('username')}</Typography>
                    <TypeDropdown label="Profile" sx={{pt: '15px'}} defaultExpanded component={<Profile />} />
                    <TypeDropdown label="Address" sx={{pt: '15px'}} defaultExpanded component={<Address />} />
                    <TypeDropdown label="Order History" sx={{pt: '15px'}} component={<OrderHistory />} />
                    <SpiritButton sx={{width: '100%', mt: '80px'}} onClick={handleLogout}>{t("Log Out")}</SpiritButton>
                </>
            )}
          </Grid>
        </ContentWrapper>
        <FooterWrapper>
            <div style={{padding: '0 15px 0 15px'}}>
                <Footer />
            </div>
        </FooterWrapper>
      </Wrapper>
    );
}

export default ProfilePage;