import {useState} from 'react'
import { styled } from "@mui/system";
import {Modal} from "@mui/material";
import SpiritSignUpModal from "./SpiritSignUpModal";
import SpiritLoginModal from "./SpiritLogInModal";
import useMediaQuery from '@mui/material/useMediaQuery';
import {CloseIcon} from '../spiritIcons';

const ModalBody = styled('form')`
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 487px;
    background-color: #ffffff;
    border-radius: 15px;
    color: #202020;
    display: flex;
    flex-direction: column;
`


const SpiritModals = ({handleCloseModal, ...props}) => {
    const [modal, setModal] = useState('Login');
    const matches = useMediaQuery('(max-width:487px)');

    const handleModal = (value) => setModal(value);
    
    const displayModal = (modalForDisplay) => {
        switch(modalForDisplay) {
            case 'Login':
                return <SpiritLoginModal handleModal={handleModal} handleCloseModal={handleCloseModal} />;
            case 'Signup':
                return <SpiritSignUpModal handleModal={handleModal} handleCloseModal={handleCloseModal} />;
            default:
                return <div>Something went wrong</div>;
        }
    }


    return (
      <Modal {...props}>
        <ModalBody style={matches ? {width: '100%', maxHeight: '580px', overflowY: 'auto', borderRadius: '0px'} : {}}>
          <CloseIcon sx={{alignSelf: 'flex-end', margin: '10px 10px 0px 0px', cursor: 'pointer'}} onClick={() => handleCloseModal(true)} />
          {displayModal(modal)}
        </ModalBody>
      </Modal>
    );
}

export default SpiritModals;