import {useState, useEffect} from 'react';

function useBlogList(id) {
    const [blogList, setBlogList] = useState(null);
    const [loading, setLoading] = useState(false);
    const [isData, setIsData] = useState(true);

    useEffect(() => {
        setLoading(true);
        fetch(`${process.env.REACT_APP_BACKEND_DOMAIN}blog`)
        .then(resp => resp.json())
        .then(json => {
            setLoading(false);
            if(!id){
                setBlogList(json.blog);
            } else {
                const blog = json?.blog?.filter(item => item.id === +id);
                if(blog && blog.length > 0){
                    setBlogList(blog[0]);
                } else {
                    setIsData(false)
                }
            }
        })
    }, [id])


    return { blogList, loading, isData };
}

export default useBlogList;

