import React, {useState} from "react";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import { Typography, Grid } from "@mui/material";
import { ExpandMoreIcon } from "../../../spiritIcons";
import { styled } from "@mui/system";
import { dbt, getCookie } from "../../../spiritUtils";
import useMediaQuery from '@mui/material/useMediaQuery';
import {useTranslation} from 'react-i18next';


const WineImage = styled("img")`
  max-height: 126px;
  max-width: 50px;
`;

const WineMobileImage = styled("img")`
  width: 100%;
  max-width: 40px;
  max-height: 126px;
`

function formatISODate(ISOstring){
    if(!ISOstring) return null;
    return ISOstring.split('T')[0].replaceAll('-', '/')
}

function getFirstProductNameAndImage(products){
    if(!products) return null
    if(products.length < 1) return null
    return {
        image: products[0].product__image,
        name: products[0].product__name
    }
}

function OrderItems(props){

    const {data, matches} = props
    const {product__color, product__id, product__image, product__name, product__price, product__type, quantity} = data;
    
    return(
        <Grid item container pl={matches ? "50px" : "5px"} pt="15px">
          <Grid container item xs={7} md={5}>
            <Grid container item>
              <Grid item container xs={3} justifyContent="center">
              {
                    matches ? 
                    <WineImage
                      src={
                        process.env.REACT_APP_BACKEND_DOMAIN + 'images/' +
                        product__image
                      }
                    />
                    :
                    <WineMobileImage
                      src={
                        process.env.REACT_APP_BACKEND_DOMAIN + 'images/' +
                        product__image
                      }
                    />
                }
              </Grid>
              <Grid item container xs={9} direction="column">
                <Grid item>
                  <Typography variant="body1" fontSize={matches ? "20px" : "16px"}>
                    <b>{dbt(product__name)}</b>
                  </Typography>
                </Grid>
                <Grid item>
                  <Typography variant="body1" fontSize={matches ? "14px" : "12px"}>
                    {dbt(product__color)} {dbt(product__type)}
                  </Typography>
                </Grid>
                <Grid item>
                  {/* <Typography variant="body1" fontSize={matches ? "20px" : "12px"}>
                    <b>Order delivered</b>
                  </Typography> */}
                </Grid>
              </Grid>
            </Grid>
          </Grid>
          <Grid
            container
            item
            xs={1}
            md={3}
            justifyContent="center"
            alignContent="center"
          >
            <Typography variant="body1" fontSize={matches ? "25px" : "16px"}>
              x{quantity}
            </Typography>
          </Grid>
          <Grid container item xs={4}>
            <Grid
              container
              item
              xs={12}
              justifyContent="center"
              alignContent="center"
            >
              <Typography variant="body1" fontSize={matches ? "35px" : "16px"}>
                {product__price}$
              </Typography>
            </Grid>
          </Grid>
        </Grid>
    )
}

const OrderItem = ({ data }) => {
  const [open, setOpen] = useState(false);
  const {t} = useTranslation();

  const {date_order, delivered, order_address, order_products, total_price, id} = data;

  const date = formatISODate(date_order);
  const {image, name} = getFirstProductNameAndImage(order_products);


  const matches = useMediaQuery("(min-width:900px)");


  return (
    <Accordion
      square
      elevation={0}
      sx={{
        width: "100%",
        backgroundColor: matches ? "#fff" : "#f5f5f5",
        "&:before": {
          display: "none",
        },
      }}
      onClick={() => setOpen(prev => !prev)}
    >
      <AccordionSummary
        sx={{
          width: "100%",
          backgroundColor: matches ? "#fff" : "#f5f5f5",
          color: "#202020",
          padding: matches ? '0 16px' : '0 10px',
        }}
      >
        <Grid item container>
          <Grid container item xs={7} md={5}>
            <Grid container item>
              <Grid item container xs={3} justifyContent="center">
                {
                    matches ? 
                    <WineImage
                      src={
                        process.env.REACT_APP_BACKEND_DOMAIN + 'images/' +
                        image
                      }
                    />
                    :
                    <WineMobileImage
                      src={
                        process.env.REACT_APP_BACKEND_DOMAIN + 'images/' +
                        image
                      }
                    />
                }
              </Grid>
              <Grid item container xs={9} direction="column">
                <Grid item>
                  <Typography variant="body1" fontSize={matches ? "25px" : "18px"}>
                    <b>{dbt(name)}...</b>
                  </Typography>
                </Grid>
                <Grid item>
                  <Typography variant="body1" fontSize={matches ? "20px" : "14px"}>
                    {order_address}
                  </Typography>
                </Grid>
                <Grid item>
                  <Typography variant="body1" fontSize={matches ? "20px" : "12px"}>
                    {date}
                  </Typography>
                </Grid>
                <Grid item>
                  <Typography variant="body1" fontSize={matches ? "20px" : "12px"}>
                    {
                        delivered ?
                        <b>{t("Order delivered")}</b>
                        :
                        <b>{t("Order not delivered")}</b>
                    }
                  </Typography>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
          <Grid container item xs={1} md={3}>
          </Grid>
          <Grid container item xs={4}>
            <Grid
              container
              item
              xs={12}
              justifyContent="center"
              alignContent="center"
            >
              <Typography variant="body1" fontSize={matches ? "35px" : "18px"}>
                <b>{total_price}$</b>
              </Typography>
            </Grid>
            <Grid
              container
              item
              xs={12}
              justifyContent="center"
              alignContent="center"
            >
              <Typography variant="body1" fontSize={matches ? "20px" : "13px"}>
                {open ? <b>{t("See less")}</b> : <b>{t("See more")}</b>}
              </Typography>
            </Grid>
          </Grid>
        </Grid>
      </AccordionSummary>
      <AccordionDetails sx={{padding: matches ? '8px 16px 16px' : '5px 10px 10px'}}>
        {/* accordion body */}
        {
            order_products &&
            order_products.map(item => {
                return <OrderItems data={item} matches={matches} key={item.product__id} />
            })
        }
      </AccordionDetails>
    </Accordion>
  );
};

export default OrderItem;
