import React, {useEffect, useState} from 'react';
import OrderItem from './OrderItem';
import { Typography } from "@mui/material";
import {useTranslation} from 'react-i18next';


const OrderHistory = () => {
    const [historyItem, setHistoryItem] = useState(null);
    const {t} = useTranslation();

    useEffect(() => {
        let controller = new AbortController();
        fetch('https://spirit.ge:8000/history/', {
            method: 'GET',
            headers: {
            'Content-Type': 'application/json;charset=utf-8',
            Authorization: `JWT ${localStorage.getItem('token')}`,
            'X-CSRFToken':  ''
            },
            signal: controller.signal
        })
        .then(res => res.json())
        .then(json => {
            setHistoryItem(json)
            controller = null
        })

        return () => {
            controller?.abort();
        }
    }, [])

    return (
        <div style={{width: '100%'}}>
            {
                historyItem &&
                historyItem.map(item => <OrderItem data={item} key={item.id} />)
            }
            {
                historyItem && historyItem.length === 0 &&
                <div style={{maxWidth: '1000px', maxHeight: '228px', display: 'flex', justifyContent: "center", alignItems: "center", height: '228px', width: '100%', backgroundColor: '#fefefe'}}>
                    <Typography variant="body1" fontSize="20px">{t("No orders found")}</Typography>
                </div>
            }
        </div>
    );
};

export default OrderHistory;