import React from 'react';
import Footer from "../footer/Footer";
import Navbar from "../navbar/Navbar";
import { useState, useEffect } from 'react';
import { styled } from "@mui/system";
import SpiritBreadcrumb from '../SpiritBreadcrumb';
import BlogCard from '../blog/BlogCard';
import useBlogList from '../CustomHooks/useBlogList';


const FooterWrapper = styled('div')`
  width: 100%;
  background-color: #202020;
  height: 100%;
  margin-top: 205px;
  color: #fbfbfb;
`

const ContentWrapper = styled('div')`
    max-width: 1770px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 75px;
`

const Wrapper = styled('div')`
    background-color: #f5f5f5;
`

const BlogPage = () => {

    const {blogList} = useBlogList(null);


    return (
        <Wrapper>
            <Navbar fixed />
            <ContentWrapper>
                <SpiritBreadcrumb path={['Home', 'Blog']} />
                {blogList && blogList.map((item, i) => {
                    if(i % 2 === 0) return <BlogCard data={item} key={item.id} />
                    return <BlogCard data={item} leftImg key={item.id} />
                })}
            </ContentWrapper>
            <FooterWrapper>
            <div style={{padding: '0 15px 0 15px'}}>
                <Footer />
            </div>
        </FooterWrapper>
        </Wrapper>
    );
};

export default BlogPage;