import { useState, useRef, useEffect, useReducer } from 'react';
import { Typography, Hidden, Grid, Box } from "@mui/material";
import { styled } from "@mui/system";
import TypeSelector from "./TypeSelector";
import RangeAccordion from "./RangeAccordion";
import CheckboxAccordion from "./CheckboxAccordion";
import ClearFilterButton from "./ClearFilterButton";
import { useHistory } from 'react-router-dom';
import useBrandList from '../CustomHooks/useBrandList';

const StyledSidebar = styled(Grid)`
    width: 100%;
    max-width: 262px;
`

const Divider = styled(Grid)`
    width: 100%;
    border-bottom: solid 2px #202020;
    height: 1px;
`



const WineFilterSidebar = (props) => {

    const {filterSettings} = props;

    const [update, forceUpdate] = useReducer(x => x + 1, 0);
    const [selectedType, setSelectedType] = useState(null);

    const { brandList } = useBrandList();

    const filters = useRef({});
    const history = useHistory();
    const selected = history.location?.state?.selectedType;
    const searchQuery = history.location?.state?.searchQuery;

    const handleFilters = (value, key) => {
        if(key !== 'clear-filters') {
          const filterObj = filters.current;
          filterObj[key] = value;
          filterSettings(filters);
          console.log(filterObj)
        } else {
          filters.current = {};
          filterSettings(filters);
          forceUpdate();
        }
    }

    useEffect(() => {
      if(searchQuery) handleFilters(searchQuery, 'search')
    }, [searchQuery])


    useEffect(() => {
      if(selected) setSelectedType(selected)
    }, [selected])

    useEffect(() => {
      console.log(history.location.state)
    })


    return (
      <StyledSidebar item container direction="column">
        <Grid item container>
          <TypeSelector
            handleFilters={(filters) => handleFilters(filters, "view")}
            types={['Wine', 'Chacha', 'Accessory', 'Exclusive']}
            selected={selectedType}
            update={update}
          />
        </Grid>
        <Divider item />
        <Grid item container>
          <RangeAccordion
            name="Price"
            min={0}
            max={300}
            handleFilters={(filters) => handleFilters(filters, "price")}
            update={update}
          />
          <RangeAccordion
            name="Alcohol"
            min={0}
            max={100}
            handleFilters={(filters) => handleFilters(filters, "alcohol")}
            update={update}
          />
          <RangeAccordion
            name="Year"
            min={1990}
            max={new Date().getFullYear()}
            handleFilters={(filters) => handleFilters(filters, "year")}
            update={update}
          />
          <CheckboxAccordion
            handleFilters={(filters) => handleFilters(filters, "color")}
            name="Color"
            checkboxes={["White", "Red", "Rose", "Other"]}
            update={update}
          />
          <CheckboxAccordion
            handleFilters={(filters) => handleFilters(filters, "region")}
            name="Region"
            checkboxes={[
              "Racha",
              "Imereti",
              "Guria",
              "Samegrelo",
              "Kakheti",
              "Adjara",
              "Kartli",
            ]}
            update={update}
          />
          <CheckboxAccordion
            handleFilters={(filters) => handleFilters(filters, "sweetness")}
            name="Sweetness"
            checkboxes={["Sweet", "Dry", "Semi-sweet", "Semi-dry"]}
            update={update}
          />
          {
            brandList.length > 0 ?
            <CheckboxAccordion
              handleFilters={(filters) => handleFilters(filters, "brands")}
              name="Brands"
              checkboxes={brandList.map(item => item.name)}
              update={update}
             />
           :
           null
          }
          <ClearFilterButton handleFilters={(filters) => handleFilters(filters, 'clear-filters')} />
        </Grid>
      </StyledSidebar>
    );
}

export default WineFilterSidebar;