import React, {useState, useEffect} from 'react';
import AddressInput from './AddressInput';
import { Typography, Grid } from "@mui/material";
import { styled } from "@mui/system";
import SpiritButton from '../../SpiritButton';
import {getCookie} from '../../../spiritUtils';
import {useTranslation} from "react-i18next";


const AddressView = props => {
    const {t} = useTranslation();
    const {handleComponentChange, desktop} = props;

    const [addresses, setAddress] = useState(null);

    const [update, setUpdate] = useState(null);


    useEffect(() => {
        const csrftoken = getCookie('csrftoken');
        fetch(`${process.env.REACT_APP_BACKEND_DOMAIN}/address`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json;charset=utf-8',
                Authorization: `JWT ${localStorage.getItem('token')}`,
                'X-CSRFToken':  csrftoken,
            },
        })
        .then(res => res.json())
        .then(json => {
            setAddress(json.items)
        })
    }, [update])

    const handleClick = () => {
        handleComponentChange('add');
    }

    return (
        <Grid item container>
            <Grid item>
                <Typography variant="body1" component="div" fontSize="20px"><b>{t("Full address, Mobile number, Full name")}</b></Typography>
            </Grid>
            <Grid item container justifyContent="center" gap="22px" mt="15px" sx={{maxHeight: '400px', overflowX: 'hidden'}}>
                {!addresses && <Typography variant="body1" component="div" fontSize="20px">{t("No addresses to display, please add them")}</Typography>}
                {addresses && addresses.map(item => <AddressInput key={item.id} data={item} handleUpdate={(id) => setUpdate(id)}/>)}
            </Grid>
            <Grid item container justifyContent={desktop ? 'flex-end' : 'center'} pt="48px">
                <SpiritButton onClick={handleClick}>{t("Add New Address")}</SpiritButton>
            </Grid>
        </Grid>
    );
};



export default AddressView;