import i18n from "i18next";
import {initReactI18next} from "react-i18next";
import en from './en';
import ge from './ge';

const resources = {
    en,
    ge
}

i18n
.use(initReactI18next)
.init({
    resources,
    fallbackLng: "en",
    lng: getLanguageFromLocalStorage(),
    interpolation: {
        escapeValue: false,
    }, 
})


i18n.on('languageChanged', (lang) => {
    localStorage.setItem('language', lang);

})

function getLanguageFromLocalStorage(){
    let storage = localStorage.getItem('language')
    if(!storage) return 'en';
    if(storage === 'en' || storage === 'ge') return storage;
    return 'en';
}

export default i18n;