import {useState, useEffect} from 'react';

function useBrandList() {
    const [brandList, setBrandList] = useState([]);

    useEffect(() => {
        fetch(`${process.env.REACT_APP_BACKEND_DOMAIN}brandlist`)
        .then(resp => resp.json())
        .then(json => {
            setBrandList(json.menu);
        })
    }, [])


    return { brandList };
}

export default useBrandList;

