import SpiritWineItem from "../carousel/SpiritWineItem";
import { DownArrowIcon, RightArrowIcon } from "../../spiritIcons";
import { styled } from "@mui/system";
import { Box, Typography, Grid, Hidden } from "@mui/material";
import SpiritButton from "../SpiritButton";
import dummyData from "../../api/ui/dummyData";
import { Carousel as LibCarousel } from "react-responsive-carousel";
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import { BlockArrowIcon } from '../../spiritIcons';
import useMediaQuery from '@mui/material/useMediaQuery';
import { useState, useEffect } from "react";
import { useHistory } from 'react-router-dom';


const StyledCarouselContainer = styled(LibCarousel)`
  & > .carousel.carousel-slider {
    display: flex;
    align-items: center;
  }
`


function pagination(data, itemsPerPage) {
  const array = [...data]
  const pages = Math.ceil(array.length/itemsPerPage)
  const result = [];
  for(let i = 0; i < pages; i++){
      result.push(array.splice(0, itemsPerPage))
  }
  return result
}


const MobileItemContainer = styled('div')`
  display: flex;
  gap: 40px;
  width: 100%;
  max-width: 709px;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
  padding: 0 15px 0 15px;
`

const LoadMore = styled(Typography)`
  cursor: pointer;
  &:hover {
    text-decoration: underline;
  }
  user-select: none;
`

const Carousel = (props) => {
  const [page, setPage] = useState(0);
  const [cartItems, setCartItems] = useState([]);

  const history = useHistory();

  useEffect(() => {
    fetch(
      `${process.env.REACT_APP_BACKEND_DOMAIN}/wineproduct/?wine=%27test%27`
    )
      .then((res) => {
        if(res.ok){
            return res.json()
        } else {
            console.error('Something went wrong')
            return false;
        }
      })
      .then((productData) => {
        if(!productData) return;       
          setCartItems(productData.menu);
      });
  }, [])


  const matchesCarousel = useMediaQuery("(max-width:1000px)"); // for desktop 3 items in carousel
  const matches = useMediaQuery("(max-width:900px)");

  const handleLoadMoreClick = () => {
    setPage(prevPage => prevPage + 1)
  }

  return (
        <>
      <Grid container mt="188px" justifyContent="center">
          <Grid item container justifyContent="center">
            <Typography variant="h3" fontSize={matches ? '28px' : '50px'}>We Recommend.</Typography>
          </Grid>
          <Grid item xs={12} pt="50px">
            {
              matches ?
              <> 
              <MobileItemContainer>
                {pagination(cartItems, 4).map((items,i) => {
                  if(i > page) return null;
                  return (
                      <>
                      {items.map(item => <SpiritWineItem wine={item} key={item.id}/>)}
                      </>
                  )
                })}
              </MobileItemContainer>
              <Grid item container justifyContent="center" pt="25px">
                <LoadMore varinat="body1" fontSize="18px" onClick={handleLoadMoreClick}><b>Load More</b></LoadMore>
              </Grid>
              </> 
              :
              <StyledCarouselContainer
                showIndicators={false} 
                showThumbs={false}
                width="100%"
                showStatus={false}
                renderArrowNext={(onClickHandler, hasNext) =>
                  <div style={{paddingRight: '15px'}}><BlockArrowIcon right onClick={onClickHandler} disabled={hasNext ? false : true} /></div>
                }
                renderArrowPrev={(onClickHandler, hasPrev) =>
                  <div style={{paddingLeft: '15px'}}><BlockArrowIcon onClick={onClickHandler} disabled={hasPrev ? false : true} /></div>
                }
              >
                {pagination(cartItems, 4).map(page => {
                  return (
                    <div style={{display: 'flex', justifyContent: 'space-around', paddingLeft: '10px', paddingRight: '10px'}}>
                      {page.map(item => <SpiritWineItem wine={item} key={item.id}/>)}
                    </div>
                  )
                })}
              </StyledCarouselContainer>
            }
          </Grid>
          <Grid item container xs={12} justifyContent="center">
            <SpiritButton sx={{ marginTop: "49px" }} mobile={matches} onClick={() => history.push('/shop')}>View All</SpiritButton>
          </Grid>
      </Grid>
    </>
  )
}



export default Carousel;



