import spiritTheme from './api/ui/spiritTheme';
import spiritThemeGE from './api/ui/spiritThemeGE';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import CssBaseline from '@mui/material/CssBaseline';
import Application from './components/Application';
import {useTranslation} from 'react-i18next';
import './locale/i18n';


// const theme = createTheme(spiritTheme);
// const themeGE = createTheme(spiritThemeGE);

function App() {
  const {i18n} = useTranslation();
  const currentTheme= i18n.language === 'ge' ? createTheme(spiritThemeGE) : createTheme(spiritTheme);
  return (
    <ThemeProvider theme={currentTheme}>
      <CssBaseline />
      <Application />
    </ThemeProvider>
  );
}

export default App;


