import { useState, useEffect } from "react";
import { Typography, Hidden, Grid, Box } from "@mui/material";
import { styled } from "@mui/system";
import { useTranslation } from "react-i18next";

const TypeSelector = (props) => {
  const { types, handleFilters, selected, update } = props;
  const [active, setActive] = useState(selected);
  const { t } = useTranslation();

  useEffect(() => {
    if(selected){
      setActive(selected);
      handleFilters(selected);
    }
  }, [selected])

  useEffect(() => {
    if(update !== 0) setActive('');
  }, [update])

  useEffect(() => {
    console.log(active)
  })


  const StyledSelector = styled(Box)`
    width: 100%;
    min-height: 60px;
    background-color: #f2f2f2;
    color: #202020;
    padding-top: 10px;
    padding-left: 15px;
    &:hover {
        border-left: solid 3px #202020;
        cursor: pointer;
    }
  `;

  const handleClick = (event) => {
    setActive(event.currentTarget.getAttribute('value'));
    handleFilters(event.currentTarget.getAttribute('value'));
  };

  return (
    <>
      {types.map((item) => (
        <StyledSelector
          component="div"
          onClick={handleClick}
          key={item}
          sx={
            item === active
              ? {
                  backgroundColor: "#202020",
                  color: "#fbfbfb",
                }
              : {}
          }
          value={item}
        >
          <Typography variant="body1" fontSize="25px">
            {t(item)}
          </Typography>
        </StyledSelector>
      ))}
    </>
  );
};

export default TypeSelector;
