import React from 'react';
import {Typography, Grid, Box} from "@mui/material";
import { styled } from "@mui/system";
import useMediaQuery from '@mui/material/useMediaQuery';
import {useHistory} from 'react-router-dom'
import {dbt} from '../../spiritUtils';
import {useTranslation} from "react-i18next";

const CardImage = styled('img')`
    width: 100%;
    max-width: 700px;
    max-height: 450px;
`

const ReadMore = styled(Typography)`
    text-decoration: underline;
    cursor: pointer;
    user-select: none;
`

const Line = styled('span')`
    width: 100%;
    border-top: solid 2px #0f0f0f;
    display: block;
    margin-top: 15px;
    margin-right: 5px;
`


const BlogCard = (props) => {
    const {t} = useTranslation();
    const {leftImg, data} = props
    const {image, id, tittle} = data;

    const matches = useMediaQuery('(min-width:900px)');
    const history = useHistory();


    const handleReadMoreClick = (id) => {
        history.push('/blogs/' + id);
    } 

    return (
      <Grid container>
        {matches ? (
          <Grid item container mt="140px" xs={12}>
            <Grid
              item
              container
              xs={6}
              justifyContent="center"
              order={leftImg ? 2 : 1}
            >
              <CardImage src={`${process.env.REACT_APP_BACKEND_DOMAIN}images/${image}`} />
            </Grid>
            <Grid
              item
              container
              xs={6}
              p={leftImg ? "0 25px 0 25px" : "0 0 0 25px"}
              order={leftImg ? 1 : 2}
            >
              <Grid item>
                <Typography variant="h3" fontSize="50px">
                  {dbt(tittle)}
                </Typography>
              </Grid>
              <Grid container item mt="10px" ml="15px" spacing="10px">
                <Grid item xs={1}>
                  <Line />
                </Grid>
                <Grid item xs={11}>
                  <Grid item>
                    <Typography
                      variant="body2"
                      fontSize="25px"
                      sx={{
                        lineHeight: "35px",
                      }}
                    >
                      Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                      Aliquam purus quam. Lorem ipsum dolor sit ame.Lorem ipsum
                      dolor sit amet, consectetur adipiscing elit. Aliquam purus
                      quam. Lorem ipsum dolor sit ame.
                    </Typography>
                  </Grid>
                  <Grid item mt="15px">
                    <ReadMore
                      component="a"
                      fontSize="25px"
                      onClick={() => handleReadMoreClick(id)}
                    >
                      {t("Read more")}
                    </ReadMore>
                  </Grid>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        ) : (
          <Grid item container mt="50px" xs={12}>
            <Grid item container justifyContent="center">
              <Typography variant="h3" fontSize="24px">
                {dbt(tittle)}
              </Typography>
            </Grid>
            <Grid
              item
              container
              xs={12}
              justifyContent="center"
              mt="23px"
            >
              <CardImage src={`${process.env.REACT_APP_BACKEND_DOMAIN}images/${image}`} />
            </Grid>
            <Grid
              item
              container
              xs={12}
              mt="48px"
            >
              <Grid container item >
                <Grid item container xs={1} alignContent="center">
                  <Line style={{maxHeight: '2px'}} />
                </Grid>
                  <Grid item xs={10}>
                    <Typography
                      variant="body2"
                      fontSize="16px"
                      sx={{
                        lineHeight: "35px",
                        padding: '15px'
                      }}
                      textAlign="center"
                    >
                      Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                      Aliquam purus quam. Lorem ipsum dolor sit ame.Lorem ipsum
                      dolor sit amet, consectetur adipiscing elit. Aliquam purus
                      quam. Lorem ipsum dolor sit ame.
                    </Typography>
                  </Grid>
                  <Grid item container xs={1} alignContent="center">
                    <Line style={{maxHeight: '2px'}} />
                  </Grid>
                <Grid item container mt="15px" justifyContent="Center">
                    <ReadMore
                      component="a"
                      fontSize="16px"
                      onClick={() => handleReadMoreClick(id)}
                    >
                      {t("Read more")}
                    </ReadMore>
                  </Grid>
              </Grid>
            </Grid>
          </Grid>
        )}
      </Grid>
    );
};

export default BlogCard;