import { useState, useRef, useEffect } from 'react';
import { Typography, Grid, Checkbox } from "@mui/material";
import { styled } from "@mui/system";
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import FormGroup from '@mui/material/FormGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import { ExpandMoreIcon } from "../../spiritIcons";
import {useTranslation} from "react-i18next";





const CheckboxAccordion = (props) => {

    const {name, handleFilters, checkboxes, update} = props

    const [Checked, setChecked] = useState([])

    const {t} = useTranslation();

    useEffect(() => {
        setChecked([])
    }, [update])
    
    const handleToggle = (value) => {
        const currentIndex = Checked.indexOf(value);
        const newChecked = [...Checked];

        if(currentIndex === -1){
            newChecked.push(value)
        } else {
            newChecked.splice(currentIndex, 1)
        }

        setChecked(newChecked)

        //pass data to parent
        handleFilters(newChecked);
    }


    return (
      <Accordion disableGutters square elevation={0} sx={{ width: "100%" }}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography variant="body2" fontSize="22px">
            {t(name)}
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          <FormGroup>
            {checkboxes.map((item) => {
            return (
              <FormControlLabel
                checked={Checked.some(checkbox => {
                  if(checkbox === item) return true;
                  return false;
                })}
                key={item}
                sx={{ "& .MuiFormControlLabel-label": { fontSize: "20px" } }}
                control={
                  <Checkbox
                    disableRipple
                    sx={{ color: "#0f0f0f" }}
                    size="medium"
                    color="secondary"
                    onChange={() => handleToggle(item)}
                  />
                }
                label={item}
              />
            )})}
          </FormGroup>
        </AccordionDetails>
      </Accordion>
    );
}

export default CheckboxAccordion;