import React from 'react';
import Footer from "../footer/Footer";
import Navbar from "../navbar/Navbar";
import { useState, useEffect } from 'react';
import { styled } from "@mui/system";
import SpiritBreadcrumb from '../SpiritBreadcrumb';
import BlogCard from '../blog/BlogCard';
import useBlogList from '../CustomHooks/useBlogList';
import { useParams, useHistory } from 'react-router-dom';
import { Grid, Typography } from '@mui/material';
import {FacebookIcon} from "../../spiritIcons"
import useMediaQuery from '@mui/material/useMediaQuery';
import {dbt} from '../../spiritUtils';
import {useTranslation} from 'react-i18next';

const FooterWrapper = styled('div')`
  width: 100%;
  background-color: #202020;
  height: 100%;
  margin-top: 205px;
  color: #fbfbfb;
`

const Image = styled('img')`
    width: 100%;
    max-width: 700px;
    max-height: 450px;
`

const ContentWrapper = styled('div')`
    max-width: 1770px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
    padding-top: 75px;
`

const Wrapper = styled('div')`
    background-color: #f5f5f5;
`

const BlogSinglePage = () => {
    const {t} = useTranslation();
    const matches = useMediaQuery('(min-width:900px)');
    const { id } = useParams();
    const {blogList, isData} = useBlogList(id);
    const history = useHistory();
    
    let blog = {image: '', tittle: '', content: ''}

    if(!isData) history.push('/')
    if(blogList) blog = blogList;
    const {image, tittle, content } = blog;


    return (
      <Wrapper>
        <Navbar fixed />
        <ContentWrapper>
          <Grid container>
            <SpiritBreadcrumb path={['Home', 'Blogs']} />
            <Grid item container pt="15px">
              <Grid item container xs={12} md={6} justifyContent="center">
                <Image
                  src={`${process.env.REACT_APP_BACKEND_DOMAIN}images/${image}`}
                />
              </Grid>
              <Grid
                item
                container
                xs={12}
                md={6}
                paddingLeft={matches ? "20px" : "0px"}
                gap="20px"
                pt="15px"
              >
                <Grid
                  container
                  item
                  justifyContent={matches ? "flex-start" : "center"}
                >
                  <Typography variant="h2" fontSize={matches ? "50px" : "25px"}>
                    {dbt(tittle)}
                  </Typography>
                </Grid>
                <Grid item>
                  <Typography
                    variant="body1"
                    fontSize={matches ? "25px" : "14px"}
                    textAlign={matches ? "start" : "center"}
                  >
                    Lorem ipsum dolor sit amet, consectetur adipiscing elit.
                    Aliquam purus quam. Lorem ipsum dolor sit ame.Lorem ipsum
                    dolor sit amet, consectetur adipiscing elit. Aliquam purus
                    quam. Lorem ipsum dolor sit ame.Lorem ipsum dolor sit amet,
                    consectetur adipiscing elit. Aliquam purus quam. Lorem ipsum
                    dolor sit ame. Lorem ipsum dolor sit ame.Lorem ipsum dolor
                    sit amet, consectetur adipiscing elit. Aliquam purus quam.
                    Lorem ipsum dolor sit ame.
                  </Typography>
                </Grid>
                <Grid item>
                  <Typography
                    variant="body1"
                    fontSize={matches ? "25px" : "12px"}
                  >
                    <FacebookIcon /> <b>{t("See more on Facebook")}</b>
                  </Typography>
                </Grid>
              </Grid>
            </Grid>
            <Grid item container pt={matches ? "206px" : "104px"}>
              <Grid container item justifyContent="center">
                <Typography variant="h2" fontSize={matches ? "50px" : "25px"}>
                  Sometimes Its more
                </Typography>
              </Grid>
              <Grid item container pt={matches ? "72px" : "31px"}>
                <Typography
                  variant="body1"
                  fontSize={matches ? "25px" : "14px"}
                  textAlign={matches ? "start" : "center"}
                >
                  {dbt(content)}
                </Typography>
              </Grid>
            </Grid>
          </Grid>
        </ContentWrapper>
        <FooterWrapper>
          <div style={{ padding: "0 15px 0 15px" }}>
            <Footer />
          </div>
        </FooterWrapper>
      </Wrapper>
    );
};

export default BlogSinglePage;