import SpiritWineItem from "../carousel/SpiritWineItem";
import { styled } from "@mui/system";
import { Box, Typography, Grid, Hidden } from "@mui/material";
import { Carousel as LibCarousel } from "react-responsive-carousel";
import 'react-responsive-carousel/lib/styles/carousel.min.css';
import { BlockArrowIcon } from '../../spiritIcons';
import useMediaQuery from '@mui/material/useMediaQuery';
import useBrandList from '../CustomHooks/useBrandList';


const StyledCarouselContainer = styled(LibCarousel)`
  & > .carousel.carousel-slider {
    display: flex;
    align-items: center;
  }
`


function pagination(data, itemsPerPage) {
  if(!data) return [];
  const array = [...data]
  const pages = Math.ceil(array.length/itemsPerPage)
  const result = [];
  for(let i = 0; i < pages; i++){
      result.push(array.splice(0, itemsPerPage))
  }
  return result
}


const MobileItemContainer = styled('div')`
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  justify-content: space-around;
`

const BrandItem = styled('img')`
    max-width: 166px;
    max-height: 108px;
`


const Brands = (props) => {
  const { brandList } = useBrandList();


  const matches = useMediaQuery("(max-width:900px)");


  return (
        <>
      <Grid container mt="188px" justifyContent="center">
          <Grid item container justifyContent="center">
            <Typography variant="h3" fontSize={matches ? '28px' : '50px'}>Our Brands.</Typography>
          </Grid>
          <Grid item xs={12} pt="50px">
            {
              matches ?
              <> 
              <MobileItemContainer>
                {brandList.map(item => (
                    <BrandItem src={`https://spirit.ge:8000/images/` + item.image} alt={item.name} />
                ))}
              </MobileItemContainer>
              </> 
              :
              <StyledCarouselContainer
                showIndicators={false} 
                showThumbs={false}
                width="100%"
                showStatus={false}
                renderArrowNext={(onClickHandler, hasNext) =>
                  <div style={{paddingRight: '15px'}}><BlockArrowIcon right onClick={onClickHandler} disabled={hasNext ? false : true} /></div>
                }
                renderArrowPrev={(onClickHandler, hasPrev) =>
                  <div style={{paddingLeft: '15px'}}><BlockArrowIcon onClick={onClickHandler} disabled={hasPrev ? false : true} /></div>
                }
              >
                {pagination(brandList, 8).map(items => {
                  return (
                    <div style={{display: 'flex', justifyContent: 'space-around', paddingLeft: '10px', paddingRight: '10px', gap: '15px'}}>
                    {
                        items.map(item => (
                            <BrandItem key={item.id} src={`${process.env.REACT_APP_BACKEND_DOMAIN}images/${item.image}`} alt={item.name} />
                        ))
                    }
                    </div>
                  )
                })}
              </StyledCarouselContainer>
            }
          </Grid>
      </Grid>
    </>
  )
}



export default Brands;