import React from "react";
import SpiritInput from "../../SpiritInput";
import SpiritButton from "../../SpiritButton";
import useMediaQuery from "@mui/material/useMediaQuery";
import { Typography, Grid } from "@mui/material";
import { useState } from "react";
import {useTranslation} from 'react-i18next';

const Profile = () => {
  const matches = useMediaQuery("(max-width:487px)");
  const {t} = useTranslation();

  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [email, setEmail] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [formErrors, setFormErrors] = useState({
    firstName: "",
    lastName: "",
    email: "",
    phoneNumber: "",
  });


  const validateInputs = () => {
    let valid = true;
    const changeFormState = (setState, newState, field) => {
        setState(prevState => {
            const stateObj = Object.assign({}, prevState)
            stateObj[field] = newState
            return stateObj;
        })
        return false;
    }
    if(firstName !== '') {
        if(firstName.length < 3 || firstName.length > 20) valid = changeFormState(setFormErrors, 'Username must be 3-20 characters long', 'firstName')
    } else {
        valid = changeFormState(setFormErrors, 'This field shouldn\'t be blank', 'firstName');
    }
    if(lastName !== '') {
        if(lastName.length < 3 || lastName.length > 20) valid = changeFormState(setFormErrors, 'Username must be 3-20 characters long', 'lastName')
    } else {
        valid = changeFormState(setFormErrors, 'This field shouldn\'t be blank', 'lastName');
    }
    if(phoneNumber !== '') {
        if(phoneNumber.length < 4) valid = changeFormState(setFormErrors, 'Phone number must be at least 4 characters long', 'phoneNumber');
    } else {
        valid = changeFormState(setFormErrors, 'This field shouldn\'t be blank', 'phoneNumber');
    }

    if(email !== '') {
        if(!email.toLowerCase().match(/\S+@\S+/g)){
            valid = changeFormState(setFormErrors, 'Please, enter valid email (Ex: mail@gmail.com)', 'email');
        }
    } else {
        valid = changeFormState(setFormErrors, 'This field shouldn\'t be blank', 'email');
    }

    return valid
}

const handleSubmit = () => {
    setFormErrors({
        firstName: "",
        lastName: "",
        email: "",
        phoneNumber: "",
    })
    const validation = validateInputs();
    if(validation){
        console.log('SUCCESS', firstName, lastName, email, phoneNumber)
    }
}

  return (
    <Grid container flexDirection="column" gap="20px">
      <Grid item>
        <SpiritInput
          label="First Name"
          value={firstName}
          error={formErrors.firstName}
          onChange={(e) => setFirstName(e.target.value)}
        />
      </Grid>
        <Grid item>
          <SpiritInput
            label="Last Name"
            error={formErrors.lastName}
            value={lastName}
            onChange={(e) => setLastName(e.target.value)}
          />
        </Grid>
        <Grid item>
            <SpiritInput
            label="Email"
            placeholder="EX: mail@gmail.com"
            value={email}
            error={formErrors.email}
            onChange={(e) => setEmail(e.target.value)}
            />
        </Grid>
      <Grid item>
        <SpiritInput
          label="Phone Number"
          error={formErrors.phoneNumber}
          value={phoneNumber}
          onChange={(e) => setPhoneNumber(e.target.value)}
        />
      </Grid>
      <Grid item container justifyContent="center">
          <SpiritButton onClick={() => handleSubmit()}>
            {t("Save")}
          </SpiritButton>
      </Grid>
    </Grid>
  );
};

export default Profile;
