import { Typography, Grid } from "@mui/material";
import { styled } from "@mui/system";
import useMediaQuery from '@mui/material/useMediaQuery';
import { ViewIcon, CartIcon } from "../../spiritIcons";
import { useHistory } from "react-router-dom";
import { dbt, getCookie } from "../../spiritUtils";

const SpiritWineItem = ({ wine, mobile, props }) => {
  const history = useHistory();
  const matches = useMediaQuery("(max-width:900px)");

  const { name, price, type, image, id, vendor, color, quantity, sales } = wine;

  function handleViewClick() {
    history.push({
      pathname: "/wine",
      state: wine,
    });
  }

  function handleCartClick() {
    const isLoggedIn = sessionStorage.getItem("logged_in");
    const csrftoken = getCookie("csrftoken");

    if(isLoggedIn) {
      fetch("https://spirit.ge:8000/cart/", {
          method: "POST",
          headers: {
            "Content-Type": "appliction/json;charset=utf-8",
            Authorization: `JWT ${localStorage.getItem("token")}`,
            "X-CSRFToken": csrftoken,
          },
          body: JSON.stringify({
            action: "quantity",
            productId: id,
            quantity: 1,
          }),
          credentials: "include",
        }).then((res) => {
          if (res.ok) {
            return res.json();
          } else {
            console.error(res.statusText)
          }
        });
    }




    if (!isLoggedIn) {
      const currentCartItem = {
        name,
        price,
        type,
        image,
        id,
        vendor,
        color,
        sales,
        inStockQuantity: quantity,
        quantity: 1,
      };
      const webStorageCart = localStorage.getItem("cartItems");

      if (webStorageCart) {
        const cartArr = JSON.parse(webStorageCart);
        const match = cartArr.filter((item) => item.id === currentCartItem.id);
        if (Array.isArray(match) && match.length) {
          const rest = cartArr.filter((item) => item.id !== currentCartItem.id);
          if (match[0].inStockQuantity > match[0].quantity) {
            match[0].quantity += 1;
            localStorage.setItem(
              "cartItems",
              JSON.stringify([...rest, match[0]])
            );
          }
        } else {
          localStorage.setItem(
            "cartItems",
            JSON.stringify([...cartArr, currentCartItem])
          );
        }
      } else {
        localStorage.setItem("cartItems", JSON.stringify([currentCartItem]));
      }
    }
  }

  const BorderWrapper = styled("div")`
    position: relative;
  `;
  
  // /////// sales styles
  const Border = styled("div")`
    height: 361px;
    width: 204px;
    border-left: solid 2px #202020;
    border-right: solid 2px #202020;
    ${
      sales ? 
      `
      ${!matches ? 
        `&:hover:after {
          border-bottom: 22px solid #f5f5f5;
        }` 
        : ``}
      &:before {
        content: "%";
        color: white;
        padding-left: 5px;
        display: inline-block;
        position: absolute;
        z-index: 2;
        height: 60px;
        width: 30px;
        right: -2px;
        background-color: #202020;
      }
      &:after {
        content: "";
        display: inline-block;
        position: absolute;
        z-index: 2;
        width: 0; 
        height: 0; 
        border-left: 15px solid transparent;
        border-right: 15px solid transparent;
        border-bottom: ${matches ? '22px' : '20px' } solid #f5f5f5;
        top: 40px;
        right: -2px;
      }`
      :
      ``
    }
    &${matches ? '' : ':hover'} {
      border-top: solid 2px #202020;
      border-bottom: solid 2px #202020;
    }
    &${matches ? '' : ':hover'} > *:not(:nth-of-type(2)):before,
    &${matches ? '' : ':hover'} > *:not(:nth-of-type(2)):after {
      width: 0px;
    }
    &${matches ? '' : ':hover'} > :nth-of-type(1):after {
      z-index: 1;
      position: absolute;
      top: 180px;
      bottom: 0;
      left: -102px;
      right: 0;
      width: 410px;
      height: 1px;
      background-color: #202020;
      transform: rotate(-61deg);
    }
    &${matches ? '' : ':hover'} > :nth-of-type(1):before {
      z-index: 1;
      position: absolute;
      top: 180px;
      bottom: 0;
      left: -102px;
      right: 0;
      width: 410px;
      height: 1px;
      background-color: #202020;
      transform: rotate(61deg);
    }
    &${matches ? '' : ':hover'} > :nth-of-type(4) {
      display: flex;
    }
  `;
  const TopBorder = styled("div")`
    display: flex;
    justify-content: space-between;
    &:before {
      display: block;
      content: "";
      width: 50px;
      border-top: 2px solid #202020;
    }
    &:after {
      display: block;
      content: "";
      width: 50px;
      border-top: 2px solid #202020;
    }
  `;
  const BotBorder = styled("div")`
    display: flex;
    justify-content: space-between;
    &:before {
      display: block;
      content: "";
      width: 50px;
      border-bottom: 2px solid #202020;
    }
    &:after {
      display: block;
      content: "";
      width: 50px;
      border-bottom: 2px solid #202020;
    }
  `;

  // deleted property height: calc(100% - 4px);
  const Content = styled("div")`
    cursor: pointer;
    position: relative;
    max-height: 361px;
    max-width: 204px;
    height: calc(100% - 4px);
    background-image: url(${process.env
      .REACT_APP_BACKEND_DOMAIN}images/${image});
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    z-index: 2;
  `;
  // const Content = styled('img')`
  //     max-height: 361px;
  // `

  const IconButton = styled("button")`
    border: none;
    background-color: #262626;
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    & > * {
      color: #fbfbfb;
    }
    &:hover {
      cursor: pointer;
      border: solid 1px #0f0f0f;
      background-color: #fbfbfb;
    }
    &:hover > :nth-of-type(1) {
      color: #202020;
    }
  `;
  const ButtonWrapper = styled("div")`
    display: none;
    gap: 12px;
    width: 132px;
    position: absolute;
    left: 34px;
    bottom: -12px;
    z-index: 2;
  `;

  const WineWrapper = styled("div")`
    width: 200px;
  `;


  // &:after {
  //   content: '';
  //   display: inline-block;
  //   position: absolute;
  //   width: 1px;
  //   height: 54px;
  //   background-color: #202020;
  //   transform: rotate(74deg);
  //   top: -16px;
  //   left: 25px;
  // }

  const OldPrice = styled(Typography)`
    &:before {
      position: absolute;
      content: "";
      left: 0;
      top: 50%;
      right: 0;
      border-top: 1px solid;
      border-color: inherit;
      transform: rotate(-10deg);
    }
  `

  return (
    <WineWrapper>
      <BorderWrapper>
        <Border>
          <TopBorder />
          <Content
            src={`${process.env.REACT_APP_BACKEND_DOMAIN}images/${image}`}
            onClick={() => handleViewClick()}
          />
          <BotBorder />
          <ButtonWrapper>
            <IconButton onClick={() => handleCartClick()}>
              <CartIcon />
            </IconButton>
            <IconButton onClick={() => handleViewClick()}>
              <ViewIcon />
            </IconButton>
          </ButtonWrapper>
        </Border>
      </BorderWrapper>
      <Grid container>
        <Grid item xs={12} mt={mobile ? "20px" : "30px"}>
          <Typography variant="body2" fontSize="20px" align="center">
            {dbt(type)} "{dbt(name)}"
          </Typography>
        </Grid>
        <Grid
          item
          container
          xs={12}
          mt={mobile ? "17px" : "27px"}
          justifyContent="center"
          alignItems="center"
          gap="15px"
        >
          {sales ? (
            <div style={{position: 'relative'}}>
              <OldPrice fontSize="14px" varaint="body1">
                {price}$
              </OldPrice>
            </div>
          ) : null}
          <Typography variant="body1" fontSize="23px" align="center">
            <b>{sales ? sales : price}$</b>
          </Typography>
        </Grid>
      </Grid>
    </WineWrapper>
  );
};

export default SpiritWineItem;
