import { useState } from 'react';
import { Typography, Hidden, Grid, Box } from "@mui/material";
import { styled } from "@mui/system";
import Footer from "../footer/Footer";
import Navbar from "../navbar/Navbar";
import { CutleryIcon, TemperatureIcon, IncreaseIcon, DecreaseIcon } from '../../spiritIcons';
import SpiritButton from '../SpiritButton';
import {dbt, getCookie} from '../../spiritUtils';
import { useTheme } from '@mui/material/styles';
import useMediaQuery from '@mui/material/useMediaQuery';


const NavbarSpace = styled('div')`
    height: 75px;
    width: '100%';
`

const WineImage = styled('img')`
    max-height: 745px;
`

const ContentWrapper = styled('div')`
    max-width: 1770px;
    margin: 0 auto;
    padding-left: 15px;
    padding-right: 15px;
`


const CardContainer = styled('div')`
    // max-height: 394px;
    // max-width: 600px;
    color: #fbfbfb;
`

const RecommendCard = styled('div')`
    background-color: #292929;
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100%;
    min-height: 435px;
`
const CardImage = styled('img')`
    position: absolute;
    width: 100%;
    display: inline-block;
    height: 100%;
`

const CardLine = styled('div')`
    width: 39px;
    height: 4px;
    background-color: #fff;
`

const FooterWrapper = styled('div')`
  width: 100%;
  background-color: #202020;
  height: 100%;
  margin-top: 205px;
  color: #fbfbfb;
`


function WinePage(props){
    const [count, setCount] = useState(1);
    const [error, setError] = useState(null);

    const csrftoken = getCookie('csrftoken');

    const isLoggedIn = sessionStorage.getItem('logged_in');


    const handleIncreaseButtonClick = () => {
      if(count >= quantity) {
        setCount(quantity)
        setError('Unfortunately, required quantity is out of stock')
        setTimeout(() => setError(null), 5000)
      } else {
        setCount(count + 1)
      }
    }

    const handleDecreaseeButtonClick = () => {
        if(count !== 1){
            setCount(count - 1)
        }
    }


    console.log(props)

    const { 
        alcoholPercent, 
        id,
        image,
        name,
        type,
        color,
        region,
        variety,
        year,
        description,
        price,
        brand_id__name,
        quantity
    } = props.location.state


    const handleAddToCartButton = () => {
        const currentCartItem = {
            name,
            price,
            type,
            image,
            id,
            quantity: 1
        }
        const webStorageCart = localStorage.getItem('cartItems');
 
        if (webStorageCart) {
          const cartArr = JSON.parse(webStorageCart);
          const match = cartArr.filter((item) => item.id === currentCartItem.id);
          if (Array.isArray(match) && match.length) {
            const rest = cartArr.filter(
              (item) => item.id !== currentCartItem.id
            );
            match[0].quantity += count;
            localStorage.setItem(
              "cartItems",
              JSON.stringify([...rest, match[0]])
            );
          } else {
            localStorage.setItem('cartItems', JSON.stringify([...cartArr, currentCartItem]));
          }
        } else {
          localStorage.setItem("cartItems", JSON.stringify([currentCartItem]));
        }
        setCount(1);
    }

    const handleLoggedInAddToCartButton = () => {

        fetch("https://spirit.ge:8000/cart/", {
          method: "POST",
          headers: {
            "Content-Type": "appliction/json;charset=utf-8",
            Authorization: `JWT ${localStorage.getItem("token")}`,
            "X-CSRFToken": csrftoken,
          },
          body: JSON.stringify({
            action: "quantity",
            productId: id,
            quantity: count,
          }),
          credentials: "include",
        }).then((res) => {
          if (res.ok) {
            console.log('succesfully added')
          }
        });
    }


    const theme = useTheme();
    const matches = useMediaQuery('(min-width:900px)');
    console.log(matches)

    const DetailsDivider = styled('div')`
    border-bottom: solid 1px #707070;
    width: 100%;
    padding-top: ${matches ? '30px' : '18px'};
    `

    return (
      <div style={{backgroundColor: '#f5f5f5'}}>
        <NavbarSpace></NavbarSpace>
        <Navbar fixed />
        <ContentWrapper>
          <Grid container>
            <Grid item container direction="row" pt="117px">
              <Grid item container xs={12} md={5} justifyContent="center">
                <WineImage src={process.env.REACT_APP_BACKEND_DOMAIN+"images/"+image}></WineImage>
              </Grid>
              <Grid item container xs={12} md={7} direction="column">
                {
                  matches ?
                  <>
                    <Typography variant="body1" fontSize="25px">
                      {dbt(type)}
                    </Typography>
                    <Typography variant="body1" fontSize="25px" pt="12px">
                      From {dbt(region)} (made in {year})
                    </Typography>
                    <Typography variant="h2" fontSize="70px" pt="12px">
                      {dbt(name)}
                    </Typography>
                  </>
                  :
                  <>
                    <Typography variant="h2" fontSize="35px" pt="12px" textAlign="center">
                      {dbt(name)}
                    </Typography>
                    <Typography variant="body1" fontSize="16px" pt="12px" textAlign="center">
                      From {dbt(region)} (made in {year})
                    </Typography>
                    <Typography variant="body1" fontSize="16px" textAlign="center">
                      {dbt(type)}
                    </Typography>
                  </>
                }
                <Grid item container direction="row" mt="46px">
                  {
                    matches ? 
                    <>
                      <Grid item xs={2}>
                        <Box
                          sx={{
                            borderTop: "solid 2px #202020",
                            mt: "10px",
                            maxWidth: "81px",
                          }}
                        />
                      </Grid>
                      <Grid item container xs={10} direction="column">
                        <Grid item>
                          <Typography variant="body1" fontSize="20px">
                            {dbt(description)}
                          </Typography>
                          <Typography variant="body1" fontSize="45px" mt="43px">
                            ${price}
                          </Typography>
                        </Grid>
                        <Grid item container direction="row" mt="46px">
                          {
                            error 
                            &&
                            <Grid item xs={12} pb="10px">
                              <Typography variant="body1" color="red" fontSize="20px">
                                {error}
                              </Typography>
                            </Grid>
                          }
                          <Grid
                            item
                            container
                            direction="row"
                            xs={3}
                            alignItems="center"
                            justifyContent="center"
                          >
                            <Grid item xs={3}>
                              <DecreaseIcon
                                onClick={() => handleDecreaseeButtonClick()}
                              />
                            </Grid>
                            <Grid item xs={6} sx={{ textAlign: "center" }}>
                              {count}
                            </Grid>
                            <Grid item xs={3}>
                              <IncreaseIcon
                                onClick={() => handleIncreaseButtonClick()}
                              />
                            </Grid>
                          </Grid>
                          <Grid item xs={9} pl="50px">
                            <SpiritButton onClick={() => {
                              if(isLoggedIn) return handleLoggedInAddToCartButton();
                              return handleAddToCartButton();
                            }}>
                              Add to Cart
                            </SpiritButton>
                          </Grid>
                        </Grid>
                      </Grid>
                    </>
                    :
                    <>
                      <Grid item container>
                        <Grid item container xs={12} justifyContent="center" spacing="10px">
                            <Grid item container xs={1} justifyContent="center" alignItems="center">
                              <span style={{
                                display: 'inline-block',
                                width: '20px',
                                height: '2px',
                                backgroundColor: "#202020"
                              }} />
                            </Grid>
                            <Grid item xs={10}>
                              <Typography variant="body1" fontSize="16px" textAlign="center">
                                {dbt(description)}
                              </Typography>
                            </Grid>
                            <Grid item container xs={1} justifyContent="center" alignItems="center">
                              <span style={{
                                display: 'inline-block',
                                width: '20px',
                                height: '2px',
                                backgroundColor: "#202020"
                              }} />
                            </Grid>
                        </Grid>
                        <Grid item container xs={12} justifyContent="center" mt="101px">
                            <Grid item xs={6}>
                              <Typography variant="body1" fontSize="45px" >${price}</Typography>
                            </Grid>
                            <Grid item container xs={6} alignItems="center" justifyContent="center">
                                <div style={{display: 'flex', maxWidth: '150px', width: "100%"}}>
                                  <Grid item container xs={3} justifyContent="center">
                                    <DecreaseIcon
                                      onClick={() => handleDecreaseeButtonClick()}
                                    />
                                  </Grid>
                                  <Grid item xs={6} sx={{ textAlign: "center" }}>
                                    {count}
                                  </Grid>
                                  <Grid container item xs={3} justifyContent="center">
                                    <IncreaseIcon
                                      onClick={() => handleIncreaseButtonClick()}
                                    />
                                  </Grid>
                                </div>
                            </Grid>
                        </Grid>
                        <Grid item container xs={12} justifyContent="center" pt="87px">
                            <SpiritButton mobile onClick={() => {
                              if(isLoggedIn) return handleLoggedInAddToCartButton();
                              return handleAddToCartButton();
                            }}>
                              Add to Cart
                            </SpiritButton>
                        </Grid>
                      </Grid>
                    </>
                  }
                </Grid>
              </Grid>
            </Grid>
            <Grid item container direction="column" mt="156px">
              <Grid item container direction="row" justifyContent="center">
                <Typography variant="h3" fontSize={matches ? '50px' : '28px'}>
                  Wine Details.
                </Typography>
              </Grid>
              <Grid item container direction="row" spacing={3} mt="76px">
                <Grid item container xs={12} md={6} direction="column">
                  <Grid item container>
                    <Grid item>
                      <Typography variant="body1" fontSize={matches ? '25px' : '16px'}>
                        Color
                      </Typography>
                    </Grid>
                    <Grid item sx={{ flexGrow: 1 }}>
                      <DetailsDivider />
                    </Grid>
                    <Grid item>
                      <Typography variant="body1" fontSize={matches ? '25px' : '16px'}>
                        {dbt(color)}
                      </Typography>
                    </Grid>
                  </Grid>
                  <Grid item container mt="42px">
                    <Grid item>
                      <Typography variant="body1" fontSize={matches ? '25px' : '16px'}>
                        Winery
                      </Typography>
                    </Grid>
                    <Grid item sx={{ flexGrow: 1 }}>
                      <DetailsDivider />
                    </Grid>
                    <Grid item>
                      <Typography variant="body1" fontSize={matches ? '25px' : '16px'}>
                        {dbt(brand_id__name)}
                      </Typography>
                    </Grid>
                  </Grid>
                  <Grid item container mt="42px">
                    <Grid item>
                      <Typography variant="body1" fontSize={matches ? '25px' : '16px'}>
                        Type
                      </Typography>
                    </Grid>
                    <Grid item sx={{ flexGrow: 1 }}>
                      <DetailsDivider />
                    </Grid>
                    <Grid item>
                      <Typography variant="body1" fontSize={matches ? '25px' : '16px'}>
                        {dbt(type)}
                      </Typography>
                    </Grid>
                  </Grid>
                  <Grid item container mt="42px">
                    <Grid item>
                      <Typography variant="body1" fontSize={matches ? '25px' : '16px'}>
                        Alcohol
                      </Typography>
                    </Grid>
                    <Grid item sx={{ flexGrow: 1 }}>
                      <DetailsDivider />
                    </Grid>
                    <Grid item>
                      <Typography variant="body1" fontSize={matches ? '25px' : '16px'}>
                        {alcoholPercent}%
                      </Typography>
                    </Grid>
                  </Grid>
                  <Grid item container mt="42px">
                    <Grid item>
                      <Typography variant="body1" fontSize={matches ? '25px' : '16px'}>
                        Region
                      </Typography>
                    </Grid>
                    <Grid item sx={{ flexGrow: 1 }}>
                      <DetailsDivider />
                    </Grid>
                    <Grid item>
                      <Typography variant="body1" fontSize={matches ? '25px' : '16px'}>
                        {dbt(region)}
                      </Typography>
                    </Grid>
                  </Grid>
                  <Grid item container mt="42px">
                    <Grid item>
                      <Typography variant="body1" fontSize={matches ? '25px' : '16px'}>
                        Variety
                      </Typography>
                    </Grid>
                    <Grid item sx={{ flexGrow: 1 }}>
                      <DetailsDivider />
                    </Grid>
                    <Grid item>
                      <Typography variant="body1" fontSize={matches ? '25px' : '16px'}>
                        {dbt(variety)}
                      </Typography>
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item container xs={12} md={6}>
                  <RecommendCard>
                    <CardContainer>
                      <CardImage
                        src="/images/abstract-lines-transparent-png.png"
                        style={{ opacity: 0.25 }}
                      ></CardImage>
                      <Grid container>
                        <Grid item container justifyContent="center" pt="22px">
                          <Typography variant="body1" fontSize={matches ? "25px" : "20px"}>
                            We Recommend
                          </Typography>
                        </Grid>
                        <Grid item container justifyContent="center" pt="29px">
                          <CardLine />
                        </Grid>
                        <Grid item container sx={{height: '100%'}} alignItems="center" pt={matches ? '81px' : '30px'} flexDirection={matches ? 'row' : 'column'}>
                          <Grid
                            item
                            container
                            xs={6}
                            direction="column"
                            alignItems="center"
                          >
                            <CutleryIcon
                              sx={{ width: "45px", height: "45px" }}
                            />
                            <Typography
                              variant="body1"
                              fontSize={matches ? "20px" : "16px"}
                              lineHeight="1.75"
                            >
                              This wine goes well with
                            </Typography>
                            <Typography variant="body1" fontSize="14px">
                              Cheese, French Bread, Meat on stove
                            </Typography>
                          </Grid>
                          <Grid
                            item
                            container
                            xs={6}
                            direction="column"
                            alignItems="center"
                          >
                            <TemperatureIcon
                              sx={{ width: "45px", height: "45px" }}
                            />
                            <Typography
                              variant="body1"
                              fontSize={matches ? "20px" : "16px"}
                              lineHeight="1.75"
                            >
                              Serving Temperature
                            </Typography>
                            <Typography variant="body1" fontSize="14px">
                              15-16 C
                            </Typography>
                          </Grid>
                        </Grid>
                      </Grid>
                    </CardContainer>
                  </RecommendCard>
                </Grid>
              </Grid>
            </Grid>
          </Grid>
        </ContentWrapper>
        <FooterWrapper>
          <ContentWrapper>
            <Footer></Footer>
          </ContentWrapper>
        </FooterWrapper>
      </div>
    );
}

export default WinePage;